@font-face {
  font-weight: bold;
  font-family: "BloggerSans";
  font-style: normal;
  src: url("../../fonts/bloggersans-bold.woff") format("woff");
}
@font-face {
  font-weight: 200;
  font-family: "BloggerSans";
  font-style: normal;
  src: url("../../fonts/bloggersans-light.woff") format("woff");
}
@font-face {
  font-weight: normal;
  font-family: "BloggerSans";
  font-style: normal;
  src: url("../fonts/bloggersans.woff") format("woff");
}
@font-face {
  font-weight: 500;
  font-family: "BloggerSans";
  font-style: normal;
  src: url("../fonts/bloggersans-medium.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "BloggerSans", sans-serif;
}
.button {
  display: block;
  text-decoration: none;
}
a {
  text-decoration: none;
  color: inherit;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.link {
  text-decoration: none;
}
.page-catalog {
  background: #edeff6;
}
.page-header {
  padding: 25px;
  padding-bottom: 8px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  background-color: #161c35;
}
.page-header--style {
  border-radius: 0 0 20px 20px;
}
.page-header__title {
  position: relative;
  margin: 56px 0 0;
  flex-basis: 100%;
  font-weight: bold;
  font-size: 36px;
  line-height: 40px;
  color: white;
}
.page-header__title::after {
  content: "";
  position: absolute;
  width: 173px;
  height: 5px;
  left: 0;
  bottom: -10px;
  background: #ff8d30;
  border-radius: 2.4px;
}
.page-header__logo-image {
  vertical-align: top;
}
.page-header__return-home {
  display: none;
}
@media (min-width: 768px) {
  .page-header {
    padding: 40px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background: #161c35;
  }
  .page-header--style {
    border-radius: 0 0 45px 45px;
    padding-bottom: 60px;
  }
  .page-header__logo-image {
    width: 166px;
    height: 42px;
  }
  .page-header__wrapper {
    display: flex;
    width: 100%;
    padding: 0 15px;
    align-items: baseline;
  }
  .page-header__title {
    margin-top: 130px;
    font-size: 60px;
    line-height: 70px;
  }
  .page-header__title::after {
    width: 287px;
    height: 8px;
    left: 0;
    bottom: -64px;
    border-radius: 4px;
  }
  .page-header__return-home {
    display: block;
    min-width: 175px;
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;
    color: white;
  }
}
@media (min-width: 1440px) {
  .page-header {
    padding: 48px 60px 20px;
  }
  .page-header__logo-image {
    width: 200px;
    height: 52px;
  }
  .page-header__wrapper {
    padding: 0 69px;
  }
  .page-header__title::after {
    bottom: -68px;
  }
}
.catalog {
  padding: 30px 25px 46px;
}
.catalog__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.catalog__show-more {
  display: block;
  margin: 25px auto 20px;
  font-weight: bold;
  font-size: 17px;
  line-height: 30px;
  color: #1d2e5b;
  text-transform: uppercase;
}
.catalog__pagination-list {
  display: none;
}
.catalog__pagination-buttons {
  display: flex;
  justify-content: space-between;
}
.catalog__pagination-prev,
.catalog__pagination-next {
  flex-basis: 49.8%;
  background: white;
  height: 50px;
  display: flex;
  justify-content: center;
}
.catalog__pagination-prev {
  border-radius: 20px 0 0 20px;
}
.catalog__pagination-next {
  border-radius: 0 20px 20px 0;
}
@media (min-width: 768px) {
  .catalog {
    padding: 40px 40px 62px;
  }
  .catalog__show-more {
    padding: 50px 0;
    font-size: 20px;
  }
  .catalog__nav {
    background: white;
    border-radius: 20px;
  }
  .catalog__nav-wrapper {
    display: flex;
    padding: 0 18px 0 28px;
    height: 100px;
  }
  .catalog__pagination-list {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    text-decoration: none;
    background: white;
  }
  .catalog__pagination-number {
    list-style: none;
    width: 50px;
    height: 50px;
    font-weight: bold;
    font-size: 20px;
    line-height: 50px;
    color: #161c35;
    border-radius: 25px;
    text-align: center;
  }
  .catalog__pagination-number--active {
    background: #ffd74b;
  }
  .catalog__pagination-buttons {
    flex-basis: 88px;
    align-items: center;
    margin-left: auto;
  }
  .catalog__pagination-prev,
  .catalog__pagination-next {
    flex-basis: 44px;
  }
}
@media (min-width: 1440px) {
  .catalog {
    padding: 77px 60px;
  }
  .catalog__wrapper {
    width: 950px;
  }
}
.user-card {
  position: relative;
  margin-bottom: 10px;
  padding: 15px 20px 22px;
  background: white;
  border-radius: 20px;
}
.user-card__visit-contries-title,
.user-card__vehicle-title,
.user-card__level-title {
  margin: 0;
  margin-bottom: 15px;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  color: #444444;
}
.user-card__foto {
  position: absolute;
  border-radius: 50%;
  width: 70px;
  height: 70px;
}
.user-card__name {
  margin: 0;
  padding: 0 0 8px 84px;
  font-weight: bold;
  font-size: 21px;
  line-height: 21px;
  color: #1d2e5b;
}
.user-card__likes {
  margin: 0;
  padding: 0 0 24px 84px;
  font-weight: bold;
  font-size: 14px;
  line-height: 21px;
  color: #161c35;
}
.user-card__hashtags {
  margin: 0;
  margin-bottom: 15px;
  padding: 0 8px;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  color: #444444;
}
.user-card__flags-list {
  padding: 0;
  list-style: none;
}
.user-card__flag {
  display: flex;
  align-items: center;
  padding-bottom: 10px;
}
.user-card__name-country {
  margin: 0;
  padding-left: 14px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
