}
.user-card__vehicle-icon {
  padding: 0 10px;
}
.user-card__level {
  flex-basis: 19.6%;
  margin-left: auto;
}
.user-card__level-number {
  display: block;
  width: 45px;
  height: 45px;
  font-weight: bold;
  font-size: 24px;
  border-radius: 50%;
  line-height: 45px;
  color: #1d2e5b;
  text-align: center;
  background: white;
  border: 3px solid #ff8d30;
}
.user-card__contact {
  margin: 0 auto;
  padding: 7px 0 3px;
  font-weight: bold;
  font-size: 17px;
  line-height: 30px;
  color: #161c35;
  background: #ffd74b;
  text-transform: uppercase;
  text-align: center;
  border-radius: 20px;
}
@media (min-width: 768px) {
  .user-card {
    padding: 35px 25px 25px 35px;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 40px;
  }
  .user-card__name,
  .user-card__likes,
  .user-card__hashtags {
    padding: 0;
  }
  .user-card__foto {
    position: relative;
    margin-bottom: 24px;
    border-radius: 10%;
    width: 190px;
    height: 190px;
  }
  .user-card__contact {
    position: absolute;
    top: 176px;
    left: 260px;
    min-width: 148px;
    line-height: 40px;
    border-radius: 25px;
  }
  .user-card__visit-contries {
    order: 1;
  }
  .user-card__name-country {
    font-size: 20px;
  }
  .user-card__wrapper {
    display: flex;
    flex-direction: column;
    padding-left: 33px;
    flex-basis: 290px;
  }
  .user-card__name {
    padding-bottom: 20px;
    font-size: 30px;
    line-height: 30px;
  }
  .user-card__likes {
    order: 1;
    padding-left: 208px;
    font-size: 20px;
    line-height: 30px;
  }
  .user-card__hashtags {
    font-size: 20px;
    line-height: 24px;
    min-height: 55px;
  }
  .user-card__wrap-info,
  .user-card__vehicle-list {
    display: flex;
    flex-direction: column;
    flex-basis: 60px;
    align-items: center;
  }
  .user-card__vehicle-title,
  .user-card__level-title {
    display: none;
  }
  .user-card__wrap-info {
    margin-left: auto;
  }
  .user-card__vehicle-icon {
    padding: 4px;
  }
  .user-card__level {
    margin: 0;
  }
  .user-card__level-number {
    width: 60px;
    height: 60px;
  }
  .user-card__flags-list {
    display: flex;
  }
  .user-card__flag {
    padding: 0;
    padding-right: 28px;
  }
