.contacts {
  padding: 50px 0 42px 50px;
  text-align: left;
}
.contacts__text {
  margin: 0;
  font-weight: 200;
  font-size: 16px;
  line-height: 22px;
  color: white;
  text-align: center;
}
.contacts__phone,
.contacts__email {
  margin: 0;
  font-weight: bold;
  font-size: 16px;
  line-height: 22px;
  color: #192144;
}
.main-nav {
  position: relative;
  width: auto;
}
.main-nav__wrapper {
  text-align: center;
}
.main-nav__toggle {
  position: absolute;
  top: -19px;
  right: 25px;
  z-index: 2;
  display: block;
}
.main-nav .social,
.main-nav .contacts {
  display: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 6px;
  left: 0;
  width: 25px;
  height: 11px;
  background: url("../../img/menu.svg");
}
.main-nav--opened .main-nav__toggle {
  content: "";
  position: absolute;
  top: -19px;
  right: 0;
  width: 19px;
  height: 19px;
  background: url("../../img/menu-close.svg");
}
.main-nav--opened .social {
  display: flex;
  align-items: center;
}
.main-nav--opened .contacts {
  display: block;
}
.main-nav__list {
  margin: 0;
  list-style: none;
}
.main-nav__contacts {
  margin: 50px 0 42px;
}
@media (max-width: 767px) {
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    width: 100%;
    min-height: 80vh;
    background-color: white;
  }
  .main-nav--closed .site-list {
    display: none;
  }
  .main-nav--closed .user-list {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
}
@media (min-width: 768px) and (max-width: 1200px) {
  .main-nav--opened {
    width: 100%;
  }
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    width: 100%;
    min-height: 80vh;
    background-color: white;
  }
  .main-nav--closed .site-list {
    display: none;
  }
  .main-nav--closed .user-list {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav {
    width: 50%;
  }
  .main-nav--closed .site-list {
    display: none;
  }
  .main-nav--closed .user-list {
    display: flex;
  }
  .main-nav__wrapper {
    display: flex;
    justify-content: flex-end;
  }
  .main-nav__toggle {
    top: 0;
    left: -12px;
    color: white;
  }
  .main-nav--closed .main-nav__toggle::before {
    top: 19px;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    width: 81.82%;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav .site-list {
    display: flex;
  }
  .main-nav__list {
    display: flex;
    justify-content: space-between;
    padding: 0;
    padding-left: 9.8%;
    align-items: center;
    color: white;
  }
  .main-nav__wrapper {
    justify-content: space-between;
  }
}
